@import url(https://fonts.googleapis.com/css2?family=Pacifico&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background-color: rgb(0, 0, 0);
}

.appTitle {
  text-align: center;
  height: 3rem;
  font-size: 2rem;
  background-color: #fdfa50;
  color: #12026b;
  font-family: "Pacifico";
}

.searchBox {
  text-align: end;
}

.searchBox input {
  border: 1px solid rgb(255, 255, 255);
  height: 2rem;
  background-color: transparent;
  border-radius: 50px;
  font-size: 1rem;
  margin: 1rem;
  color: white;
}

.movieContainer {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  margin: 4.5rem;
  transition: ease;
}

.pageButtons {
  text-align: center;
  padding: 1rem;
}

.pageButtons button {
  width: 8rem;
  height: 2rem;
}

.pageOne {
  color: white;
}

.imgContainer {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  max-width: 250px;
  max-height: 450px;
  margin: 1rem;
  background-color: rgb(94, 209, 66);
  position: relative;
  overflow: hidden;
  box-shadow: 2px 2px 5px rgba(243, 97, 39, 0.2);
  border-bottom-left-radius: 6px;
  border-bottom-right-radius: 6px;
}

.imgContainer img {
  height: 400px;
  overflow: hidden;
}

.movieInfo {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  height: 4rem;
}

.movieInfo h3 {
  padding: 5px;
  overflow: hidden;
}

.overView {
  position: absolute;
  bottom: 0;
  right: 0;
  left: 0;
  background-color: rgb(155, 243, 96);
  padding: 1rem;
  max-height: 400px;
  overflow: hidden;
  -webkit-transform: translateY(100%);
          transform: translateY(100%);
  transition: 0.5s ease-in-out;
}

.imgContainer:hover .overView {
  -webkit-transform: translateY(0%);
          transform: translateY(0%);
}

